.contact-me-form {
  width: 300px;
  padding: 20px;
  margin-inline: auto;

  background-color: rgb(24,24,26);

  border-radius: 10px;

  margin-top: 2rem;
}

.contact-me-form__title {
  color: rgb(239,239,241);
  text-align: center;
  margin-bottom: 1.5rem;
}

.contact-me-form__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.3rem;
}

.contact-me-form__item-label {
  font-size: 12px;
  color: var(--dark-gray-color);
  color: rgb(239,239,241);

  margin-bottom: 0.3rem;
}

.contact-me-form__input {
  outline: none;
}

.contact-me-form__item-message {
  resize: none;
  height: 150px;

  outline: none;
}

.contact-me-form__submit-button-container {
  margin-top: 3em;
}
.contact-me-form__submit-button {
  color: white;
  background-color: rgb(15,13,16);

  font-size: 14px;
  padding: 0.3rem 1rem 0.3rem 1rem;
  border-style: solid;
  border-color: white;
  border-width: 0.05rem;

  cursor: pointer;

  /*Align the Submit Button to the middle of the contact me form*/
  display: block;
  margin-inline: auto;
  
}

.contact-me-form__submit-button:hover {
  font-weight: 500;
}

.contact-me-form__confirmation {
  margin-top: 1.5rem;
  color: white;
  font-size: 15px;
  text-align: center;
}